.todo {
    border: 2px solid #cfcdcd;
    border-radius: 4px;
    padding: 2rem;

    & + & {
        margin-top: 2rem;
    }

    &__title {
        margin: 0;
        font-size: 22px;
        font-weight: bold;
    }

    &__description {
        margin-bottom: 1rem;
        display: none; 

        &-text {
            margin-top: 0;                      
        }

         &.is-visible {
            display: block;
        }
    }

    &__button-group {
        display: flex;

        &--right {
            justify-content: flex-end;
        }
    }

    &__button {
        & + & {
            margin-left: 1.5rem;
        }

        position: relative;
        display: inline-block;
        box-sizing: border-box;
        border: none;
        border-radius: 4px;
        padding: 0 16px;
        min-width: 64px;
        height: 36px;
        vertical-align: middle;
        text-align: center;
        text-overflow: ellipsis;
        text-transform: uppercase;
        color: rgb(255, 255, 255);
        background-color: rgb(33, 150, 243);
        box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
        font-size: 14px;
        font-weight: 500;
        line-height: 36px;
        overflow: hidden;
        outline: none;
        cursor: pointer;
        transition: box-shadow 0.2s;

        &--error {
            background-color: rgb(243, 33, 61);
        }

        &--more {
            background-color: rgb(131, 33, 243);
        }

        &::-moz-focus-inner {
            border: none;
        }

        /* Overlay */
        &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background-color: rgb(255, 255, 255);
            opacity: 0;
            transition: opacity 0.2s;
        }

        /* Ripple */
        &::after {
            content: "";
            position: absolute;
            left: 50%;
            top: 50%;
            border-radius: 50%;
            padding: 50%;
            width: 32px; /* Safari */
            height: 32px; /* Safari */
            background-color: rgb(255, 255, 255);
            opacity: 0;
            transform: translate(-50%, -50%) scale(1);
            transition: opacity 1s, transform 0.5s;
        }

        /* Hover, Focus */
        &:hover,
        &:focus {
            box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
        }

        &:hover::before {
            opacity: 0.08;
        }

        &:focus::before {
            opacity: 0.24;
        }

        &:hover:focus::before {
            opacity: 0.3;
        }

        /* Active */
        &:active {
            box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
        }

        &:active::after {
            opacity: 0.32;
            transform: translate(-50%, -50%) scale(0);
            transition: transform 0s;
        }

        /* Disabled */
        &:disabled {
            color: rgba(0, 0, 0, 0.38);
            background-color: rgba(0, 0, 0, 0.12);
            box-shadow: none;
            cursor: initial;
        }

        &:disabled::before {
            opacity: 0;
        }

        &:disabled::after {
            opacity: 0;
        }        
    }
}





